  --bg-color-main: #fafafa;
  --bg-color-primary: #dddbdb;
  --bg-color-secondary: #8f8e8e;
  --bg-color-accent: rgba(61, 174, 226, 0.322);
  --bg-gradient-top-bottom: rgba(255, 255, 255, 0.8) linear-gradient(to top, #ffffff 0%, transparent 50%, #ffffff 100%);
