  --icon-img-color--hover: hsl(196, 5%, 61%);
  --icon-bg-color: hsl(192, 3%, 70%);
}

.dark-theme {
  --text-color-primary: #fafafa;
  --bg-color-main: #101518;
  --linear-gradient-to-right: linear-gradient(to right, #0e0e0f 25%, transparent 100%);
  --icon-img-color: hsl(197, 3%, 47%);
